import React from "react";
import { Link } from "react-router-dom";
import styles from "./user-list-item.module.scss";

interface EntryProps {
  name: string;
  value: string;
}

function Entry({ name, value }: EntryProps) {
  return (
    <div>
      <dt className={styles.entryName}>{name}</dt>
      <dd className={styles.entryValue}>{value}</dd>
    </div>
  );
}

export interface UserListItemProps {
  id: number;
  name: string;
  city: string;
  company: string;
}

export default function UserListItem({
  id,
  name,
  city,
  company,
}: UserListItemProps) {
  return (
    <li className={styles.container}>
      <dl className={styles.entryList}>
        <Entry name="–§–ò–û" value={name} />
        <Entry name="–≥–æ—Ä–æ–¥" value={city} />
        <Entry name="–∫–æ–º–ø–∞–Ω–∏—è" value={company} />
      </dl>
      <Link to={`/${id}`} className={styles.profileLink}>
        –ü–æ–¥—Ä–æ–±–Ω–µ–µ
      </Link>
    </li>
  );
}
